@charset "utf-8";

@include pc {};
@include sp {};

.p-mypage {
    width: 100%;
    margin: 0 auto;

    &__bg {
        background: #2a3132;  /* fallback for old browsers */
        @include pc {
          width: 1800px;
          height: 280px;
        };
        @include sp {
          width: 100%;
          height: 150px;
        };
    }

    &__user {
        min-height: 300px;
        text-align: center;
        margin: 0 auto;
        @include pc {
          width: 500px;
        };
        @include sp {};

        &-img {
            display: block;
            margin-top: -100px;
            margin-bottom: 20px;
        }

        &-name {
            font-size: 2rem;
            margin-bottom: 20px;
        }

        &-change {
            &__img {
                position: absolute;
                display: inline-block;
                top: 18em;
                margin-left: 120px;
                color: #000;
                font-size: 1.3em;
            }
            &__info {
              display: block;
              width: 100%;
              margin: 30px 0;
              #text {
                word-wrap: break-word !important;
                display: block;
                width: 100%;
  
              }
          }
        }
    }
    &__tab {
      padding: 20px 0;
      // border-bottom: solid 1px #e8eef5;
      border-top: solid 1px #e0e7ef;
      margin: 50px auto;  

      @include pc {
        width: 1000px;
        min-height: 400px;
      };
      @include sp {
        width: 100%;
        overflow: scroll;
      };
    }
}

