.tabs {
    margin-top: 50px;
    position: absolute;
    padding-bottom: 40px;
    margin: 0 auto;
    @include pc {
      width: 1000px;
    };
    @include sp {};
  }
  
  /*ã¿ãã®ã¹ã¿ã¤ã«*/
  .tab_item {
    height: 50px;
    background-color: #f3f2f2;
    line-height: 50px;
    text-align: center;
    color: #565656;
    display: block;
    float: left;
    text-align: center;
    font-weight: bold;
    transition: all 0.1s ease;
    min-width: calc(100%/4);
    @include pc {
      font-size: 16px;
    };
    @include sp {
      font-size: .9rem;
      overflow: scroll;
    };
  }
  .tab_item:hover {
    opacity: 0.75;
  }
  
  /*ã©ã¸ãªãã¿ã³ãå¨ã¦æ¶ã*/
